const feedbackSuccess = document.querySelector(".form-feedback--success"); 
const feedbackFailure = document.querySelector(".form-feedback--fail"); 
const form = document.querySelector('.form');
const FEEDBACK_EMAIL_STATUS_CLASS = "form-feedback--show"

function showSuccessMessage(){
    feedbackSuccess.classList.add(FEEDBACK_EMAIL_STATUS_CLASS);
}

function showFailureMessage(){
    feedbackFailure.classList.add(FEEDBACK_EMAIL_STATUS_CLASS);
}

function sendEmail(){
    const formData = new FormData(form);
    // formData.get("name") -> "Firstname Lastname"
    // formData.get("email") -> "user@mail.com"
    // formData.get("subject") -> "test"
    // formData.get("emailtext") -> "lorem ipsum"

    // TODO: Endpoint update
    fetch('http://TODO', {
        method: 'POST',
        body: formData,
        // TODO... CORS and stuff
        headers: {
            'Access-Control-Allow-Origin': 'http://TODO'
        }
    })
    .then(response => response.json())
    .then(result => {
        console.log("RESULT", result);
        showSuccessMessage();
        form.reset();
    })
    .catch(error => {
        console.log("ERROR", error);
        showFailureMessage();
    });
}

// Only do things on submit
form.addEventListener('submit', function(event){
    event.preventDefault();
    sendEmail();
});
